import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useParams } from 'react-router-dom';
import { SET_DATA } from 'store/actions';

// material-ui
import { Grid, Table, TableContainer, TableBody, TableHead, TableRow, TableCell, Typography, Slider, TextField, Chip } from '@material-ui/core';
import StockChartCard from 'views/dashboard/Default/StockChartCard';
import MainCard from 'ui-component/cards/MainCard';
// project imports
import { gridSpacing } from 'store/constant';

import StockCard from 'views/dashboard/Default/StockCard';
import SubCard from 'ui-component/cards/SubCard'
import configData from 'config';
import { makeStyles, useTheme } from '@material-ui/styles';

import axios from 'axios';
//==============================|| TYPOGRAPHY ||==============================//

const StrategiesPage = () => {
    const [isLoading, setLoading] = useState(true);
    const [dataset, setDataset] = useState({});
    const account = useSelector((state) => state.account);
    let { id } = useParams();
    const [value, setValue]  = useState([0, 100]);
    const [tempValue, setTempValue]  = useState([0, 100]);
    const theme = useTheme();

    const handleSubscribe = (id) => {
        const url = configData.API_SERVER+"users/strategy_subscribe";
        axios.post(url, {}, { headers: { Authorization: `${account.token}` }, params: {strategy_id: id} })
            .then((response) => console.log(response))

        setDataset({...dataset, subscribed: true});
    }

    const handleUnsubscribe = (id) => {
        const url = configData.API_SERVER+"users/strategy_unsubscribe";
        axios.post(url, {}, { headers: { Authorization: `${account.token}` }, params: {strategy_id: id} })
            .then((response) => console.log(response))
        
        setDataset({...dataset, subscribed: false});
    }

    const handleChange = (t) =>{
        setValue(tempValue);
    }

    const handleChangeTemp = (t) =>{
        setTempValue(t.target.value);
    }

    const formatter = (x) =>{
        let day = Math.floor(x*dataset["results"]["graphs"]["chart"].length/100);
        if (day == dataset["results"]["graphs"]["chart"].length){
            day = day-1;
        }
        return (new Date(dataset["results"]["graphs"]["chart"][day][0]).toLocaleDateString());
    }

    useEffect(() => {
        const url = configData.API_SERVER+"strategies/"+id;
        axios.get(url, { headers: { Authorization: `${account.token}` } })
          .then((response) => setDataset(response.data))
          .then(() => setLoading(false))
    }, []);


    return (
            <Grid container spacing={gridSpacing} justifyContent="center">
                {(!dataset.results || !dataset["results"]["graphs"]["chart"].length || !dataset["results"]["graphs"]["benchmark"].length) ? 
                <Typography variant="h1"></Typography> : 
                <Grid item lg={8} xs={12} justifyContent="center" align="center">
                    <MainCard>
                        <Grid container>
                            <Grid item xs={12} md={6}>
                                <Typography variant="h1" align="left">
                                    {dataset.name}
                                </Typography>
                            </Grid>
                            <Grid item xs={12} md={6}>
                                <Typography variant="h1" align="right">
                                    {!dataset.subscribed ? 
                                    <Chip label="Subscribe" variant="outlined" onClick={() => handleSubscribe(dataset.id)}/>
                                    : <Chip label="Unsubscribe" onClick={() => handleUnsubscribe(dataset.id)}/>
                                    }
                                </Typography>
                            </Grid>
                        </Grid>
                        <StockChartCard 
                            white={true} 
                            dataset={dataset["results"]["graphs"]["chart"].slice(value[0]*dataset["results"]["graphs"]["chart"].length/100, value[1]*dataset["results"]["graphs"]["chart"].length/100)}
                            benchmark={dataset["results"]["graphs"]["benchmark"].slice(value[0]*dataset["results"]["graphs"]["benchmark"].length/100, value[1]*dataset["results"]["graphs"]["benchmark"].length/100)} 
                        />
                    </MainCard>
                    <Grid item xs={10} justifyContent="center" >
                    <Slider
                        sx={{ mt: 6}}
                        getAriaLabel={() => 'Date range'}
                        value={tempValue}
                        onChange={handleChangeTemp}
                        onChangeCommitted={handleChange}
                        valueLabelDisplay="on"
                        valueLabelFormat={(x) => formatter(x)}
                        disableSwap
                        // getAriaValueText={valuetext}
                        />
                        </Grid>

                </Grid>}
                {dataset.results ?
                <Grid container item xs={12} justifyContent="center" spacing={gridSpacing}>
                    <Grid item xs={12} lg={6} justifyContent="center">
                        <SubCard>
                            <Grid container direction="row" justifyContent="space-evenly" alignItems="center">
                                <Grid item xs={12}>
                                    <Typography variant="h1" >
                                        Recent Trades
                                    </Typography>
                                </Grid>
                                <Grid container item xs={12} justifyContent="center">
                                {dataset.results ? 
                                <TableContainer>
                                    <Table>
                                        <TableHead>
                                            <TableRow>
                                                <TableCell align="left">Asset</TableCell>
                                                <TableCell align="left">Quantity</TableCell>
                                                <TableCell align="left">P/L</TableCell>
                                                <TableCell align="left">Entry Time</TableCell>
                                                <TableCell align="left">Exit Time</TableCell>
                                            </TableRow>
                                        </TableHead>
                                        <TableBody>
                                        {(dataset.results.graphs.closed_positions).map((row) => (
                                            <TableRow
                                            key={row.asset+row.exit_time}
                                            sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                            >
                                                <TableCell align="left" component="th" scope="row">{row.asset}</TableCell>
                                                <TableCell align="left">{row.qty}</TableCell>
                                                <TableCell align="left">{row.pl_dollars}</TableCell>
                                                <TableCell align="left">{row.entry_time}</TableCell>
                                                <TableCell align="left">{row.exit_time}</TableCell>
                                            </TableRow>
                                        ))}
                                        </TableBody>
                                    </Table>
                                </TableContainer>: <></>}
                                </Grid>
                            </Grid>
                        </SubCard>
                    </Grid>
                    <Grid item xs={12} lg={6} justifyContent="center">
                        <SubCard>
                            <Typography variant="h1" sx={{ mb: 4 }}>
                                Details
                            </Typography>
                            <Grid container spacing={gridSpacing} sx={{ mb: 4 }}>
                                <Grid item xs={6} justifyContent="center" align="center">
                                    <Typography display="inline" variant="h1" sx={{ fontWeight: 'regular'}}>
                                        {dataset.results ? dataset.results.stats.score: 0} 
                                    </Typography>
                                    <Typography display="inline" variant="h4">/5</Typography>
                                    <Typography variant="h2" sx={{ color: theme.palette.secondary.dark }}>
                                        Risk Score
                                    </Typography>
                                </Grid>
                                <Grid item xs={6} justifyContent="center" align="center">
                                    <Typography variant="h1" sx={{ fontWeight: 'regular' }}>
                                        {dataset.results ? dataset.results.stats.annualized_return: 0}%
                                    </Typography>
                                    <Typography variant="h2" sx={{ color: theme.palette.secondary.dark }}>
                                        Annualized Return
                                    </Typography>
                                </Grid>
                                <Grid item xs={6} justifyContent="center" align="center">
                                    <Typography variant="h1" sx={{ fontWeight: 'regular' }}>
                                        {dataset.results ? dataset.results.stats.annualized_sharpe: 0}
                                    </Typography>
                                    <Typography variant="h2" sx={{ color: theme.palette.secondary.dark }}>
                                        Sharpe
                                    </Typography>
                                </Grid>
                                <Grid item xs={6} justifyContent="center" align="center">
                                    <Typography variant="h1" sx={{ fontWeight: 'regular' }}>
                                        {dataset.results ? dataset.results.stats.mdd: 0}
                                    </Typography>
                                    <Typography variant="h2" sx={{ color: theme.palette.secondary.dark }}>
                                        Max Drawdown
                                    </Typography>
                                </Grid>
                            </Grid>
                            <Grid container direction="row" justifyContent="space-evenly" alignItems="center" spacing={gridSpacing}>
                                <Grid item xs={12}>
                                    <Typography variant="h2">
                                        Description
                                    </Typography>
                                    <Typography variant="h4" sx={{ fontWeight: 'regular' }}>
                                        {dataset.description}
                                    </Typography>
                                </Grid>
                                <Grid container item justifyContent="center" xs={12}>
                                    <TextField fullWidth id="outlined-basic" label="Shareable URL" variant="outlined" value={"https://quantria.io/utils/strategies/"+dataset["id"]} disabled />
                                </Grid>
                            </Grid>
                        </SubCard>
                    </Grid>

                </Grid>: <></>}

                {/* {dataset.map(item => (
                    <Grid item key={item.name} xs={12} md={6} lg={4}>
                        <StockCard isLoading={isLoading} data={item}/>
                    </Grid>
                ))} */}
            </Grid>
    );
};

export default StrategiesPage;
